html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: ’’;
  content: none;
}
/* remember to define visible focus styles! 
:focus{
    outline:?????;
} */

/* remember to highlight inserts somehow! */
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*------------------------------------*\
    $MAIN
\*------------------------------------*/

@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,500);

body {
  /* margin: 0 auto; */
  /* width: 90%; */
  /* max-width: 1240px; */
  font-family: "Roboto", sans-serif;
  /* background-color: rgb(136, 213, 221); */
}

/* Typography */

h1 {
  font-size: 30px;
  /* font-weight: 300; */
  /* flex: 1; */
}

h5 {
  font-weight: 500;
  line-height: 1.7em;
}

h6 {
  color: #666;
  font-size: 14px;
}

button {
  border: none;
  border-radius: 8px;
  background-color: rgb(136, 213, 221);
}

button:hover {
  cursor: pointer;
  background-color: rgb(97, 181, 189);
}

label {
  color: #666;
  font-weight: 500;
  line-height: 2em;
  text-transform: uppercase;
}

select {
  background-color: white;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 1px 5px;
  border-radius: 5px;
  height: 25px;
}

select:hover {
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 1px 10px;
}

@media (max-width: 480px) {
  h1 {
    margin-bottom: 20px;
  }
}
